@import "./src/common/styles/sass/variables";
@import "./src/common/styles/sass/mixins";

.nav {
  border: 1px solid;
  @include flex(flex-end, center, row, "", 20px);
  margin-right: 100px;

  a {
    font-family: $fontFamily;
    font-weight: 500;
    text-transform: uppercase;
    text-align: center;
    color: #dfdfdf;
    cursor: pointer;

    &:hover {
      color: $primaryColor;
    }
  }

  .active {
    font-family: $fontFamily;
    font-weight: 500;
    text-transform: uppercase;
    text-align: center;
    color: $primaryColor;
  }

  @media screen and (max-width: 530px) {
    display: none;
  }
}
