@import "src/common/styles/sass/mixins";
@import "src/common/styles/sass/variables";

.contactBlock {
  padding: 50px 0 50px 0;
  //background: linear-gradient(0deg, #020312, rgba(2, 20, 35, 0.60)), url("src/assets/images/background/videoblocks.png") center;
  background: linear-gradient(0deg, #020312, rgba(2, 20, 35, 0.6)) center;
  background-size: cover;

  .contactsContainer {
    @include flex("", "", column, "", "");

    .contactForm {
      max-width: 500px;
      width: 100%;
      @include flex(center, center, column, "", 10px);

      textarea {
        padding: 10px;
        font-family: "Open Sans", sans-serif;
        color: #dfdfdf;
        border: none;
        background: rgba(139, 145, 150, 0.2);
        resize: none;
        min-height: 100px;
        width: 100%;
      }
      input {
        padding: 10px;
        font-family: "Open Sans", sans-serif;
        letter-spacing: 1px;
        color: #dfdfdf;
        border: none;
        background: rgba(139, 145, 150, 0.2);
        width: 100%;
      }
    }

    @media screen and (max-width: 406px) {
      .contactForm {
        width: 100%;
        textarea {
          width: 250px;
        }
        input {
          width: 250px;
        }
      }
    }
  }
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 0;
  -webkit-text-fill-color: #dfdfdf;
  -webkit-box-shadow: 0 0 0 1000px rgba(139, 145, 150, 0) inset;
  transition: background-color 5000s ease-in-out 0s;
  background: -webkit-linear-gradient(
    top,
    rgba(255, 255, 255, 0) 0%,
    rgba(0, 174, 255, 0.04) 50%,
    rgba(255, 255, 255, 0) 51%,
    rgba(0, 174, 255, 0.03) 100%
  );
}
